import logo from './logo.svg';
import './App.css';
import './reset.css';
import Calendar from './Calendar/Calendar';
import Year from './Year/Year';
import Month from './Month/Month';
import Day from './Day/Day';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { Link, NavLink } from 'react-router-dom'; //   
import { useEffect, useState } from 'react';

function App() {

  const [date, setDate] = useState(new Date());
  const [tasksOfYears, setTasksOfYears] = useState([])
  const [tasksOfMonths, setTasksOfMonths] = useState([])
  const [tasksOfDays, setTasksOfDays] = useState([])

  useEffect(()=> {//    (  )
    setTasksOfYears([
      {year: 2024, text:'  ', color: 'blue', icon: true, opacity: 1},
      {year: 2024, text:'  ', color: 'green', icon: true, opacity: 1},
      {year: 2024, text:' ', color: 'green', icon: true, opacity: 1},
      {year: 2025, text:' ', color: 'green', icon: true, opacity: 1},
      {year: 2025, text:' ', color: 'grey', icon: true, opacity: 1},
    ])
    setTasksOfMonths([
      {data: '11/2024', text: '  ', color:'green', icon: true, opacity: 1},
      {data: '11/2024', text: ' ', color:'grey', icon: true, opacity: 1},
      {data: '12/2024', text: ' ', color:'blue', icon: true, opacity: 1},
      {data: '12/2024', text: '', color:'green', icon: true, opacity: 1},
      {data: '1/2025', text: ' ', color:'blue', icon: true, opacity: 1},
    ])
    setTasksOfDays([
      {text: '  ', color: 'blue', icon: true, opacity: 1, number: '15/12/2024'}, 
      {text: ' ', color: 'green', icon: true, opacity: 1, number: '20/12/2024'},
      {text: ' ', color: 'grey', icon: true, opacity: 1, number: '20/12/2024'},
      {text: ' ', color: 'green', icon: true, opacity: 1, number: '31/12/2024'}, 
      {text: ' ', color: 'green', icon: true, opacity: 1, number: '02/1/2025'},
      {text: ' ', color: 'grey', icon: true, opacity: 1, number: '10/1/2025'},
      {text: '  ', color: 'green', icon: true, opacity: 1, number: '17/1/2025'},
    ])
  }, [])


  return (
    <Router>
<Routes>
  <Route path='/' element={<Year date={date} setDate={setDate} tasksOfYears={tasksOfYears} setTasksOfYears={setTasksOfYears} tasksOfMonths={tasksOfMonths} setTasksOfMonths={setTasksOfMonths}/>}/>
  <Route path='/month' element={<Month date={date} setDate={setDate} setTasksOfDays={setTasksOfDays} tasksOfDays={tasksOfDays}  tasksOfYears={tasksOfYears} setTasksOfYears={setTasksOfYears} tasksOfMonths={tasksOfMonths} setTasksOfMonths={setTasksOfMonths}/>}/>
  <Route path='/day' element={<Day date={date} setDate={setDate} setTasksOfDays={setTasksOfDays} tasksOfDays={tasksOfDays} tasksOfYears={tasksOfYears} setTasksOfYears={setTasksOfYears} tasksOfMonths={tasksOfMonths} setTasksOfMonths={setTasksOfMonths}/>}/>
</Routes>
</Router>
  );
}

export default App;

